import { Component } from '@angular/core';
import { 
NavController,
AlertController, LoadingController } from 'ionic-angular';

import { TabsPage } from '../tabs/tabs';

import { UserService } from '../../services/mocks/user.service'; 

@Component({
	selector : 'page-login',
	templateUrl : 'login.html'
})

export class LoginPage{
	
	user = { email :'', password : ''}

	constructor(private alertCtrl:AlertController, public loadingCtrl:LoadingController, public navCtrl:NavController, private userService:UserService){


	}

	ngOnInit(){
		console.log('arranco el inti');
		//si el usuairo esta logueado redireccionar a home
	}

	login=():void=>{
		if(this.user.email !='' && this.user.password !=''){

			let usuarios;

			let loading = this.loadingCtrl.create({
			content:'please wait....'
			});
			loading.present();

			let login=false;
			this.userService.loginUser(this.user.email, this.user.password)
			.then(
				(response)=>{
					loading.dismiss();
					if( response !== undefined){
						this.navCtrl.push(TabsPage);
					}else{
						let alert = this.alertCtrl.create({
							title:'Login',
							subTitle:'Usuario y/o contraseña incorrecta',
							buttons: ['Aceptar']
						});
						alert.present();
					}

				}
			);

		}else{
			let alert = this.alertCtrl.create({
				title:'Login',
				subTitle:'Usuario y/o contraseña incorrecta',
				buttons: ['Aceptar']
			});
			alert.present();
		}

	}

} 


