import { Injectable } from '@angular/core';

import { User } from '../../commons/user';
import { USERS } from '../mocks/users';

import { Http, Response, Headers, RequestOptions } from '@angular/http';

import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
  
@Injectable()

export class UserService{
	
	constructor(private http: Http){}


	  public post(url:string, parameters:any) {
    let headers = new Headers({'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8', 'Accept': 'application/jsonp'});
    headers.append('Accept', 'application/jsonp');
    headers.append('Content-Type', 'application/jsonp');
    let options = new RequestOptions({ headers: headers});
    return this.http.post(url, parameters, options)
                  .map(this.extractData)
                  .catch(this.handleError);
  }

  private extractData(res: Response) {
    let body = res.json();
    return body || { };
  }

  private handleError (error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Promise.reject(errMsg);
  }



	getUsers():Promise<User[]>{
		console.log('entro a promise');
		return Promise.resolve(USERS);
	}

	loginUser(email,password):Promise<User>{
		let user:User;
		//let json = {usuario:'58593',clave:'00000',token:'',consulta:'LOGIN',isRedirect:'NO',pageRedirect:'',pageError:''});
		let json='usuario=58593&clave=00000&token=&consulta=LOGIN&isRedirect=NO&pageRedirect=&pageError=';
		//this.post('http://localhost/avantel/appServices/login.php', {email:email,password:password})
		this.post('https://www.avantel.co/funcionalidadmiavantel/login.du',json)
					.subscribe(
			(result) => {
			console.log(result,'resultado request');
				switch (result.responseCode) {
					case '404':
						console.log('error 404',"Ocurrió un error");
						user = undefined;
						break;
					case '200':
						//this.serviceLogin.setSession(result.data);
						//this.router.navigate(['form']);
						user={
								nick_name : 'nick_name',
								full_name : 'full_name',
								email : email,
								password : password,
						}
						break;
				}
			},
			(error) =>  {
				console.log(error,'error');
				user = undefined;
			});
		//user = this.find(email,password);
		return Promise.resolve(user);
	}

	find(email,password):User{
		let user:User;
		user = USERS.find(x => x.email == email);
		console.log(user,'cuando no es encontrado ');
		if(user != undefined){
			if(user.password == password){
				return user;
			}else{
				return undefined;
			}
		}
	}
}


